import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import Multiselect from 'multiselect-react-dropdown';
import axios from 'axios';
import DateTimePicker from 'react-datetime-picker';
import { readParticipants, createInterview } from '../utilities/url';

const AddScreen = () => {
  const [loading, setLoading] = useState(true);
  const [startTime, setStartTime] = useState(new Date());
  const [endTime, setEndTime] = useState(new Date());
  const [participantList, setParticipantList] = useState([]);
  const [userList, setUserList] = useState([]);

  const navigate = useNavigate();

  useEffect(() => {
    async function getParticipantsDetails() {
      try {
        const getParticipants = await axios.get(readParticipants);
        // console.log(getParticipants.data);
        setLoading(false);
        setUserList(getParticipants.data);
      } catch (error) {
        console.log('error');
      }
    }
    getParticipantsDetails();
  }, []);

  // const check = (email) => {
  //   return emailsList.includes(email);
  // };

  const submit = async (e) => {
    e.preventDefault();
    try {
      if (startTime === null || endTime === null || startTime > endTime)
        return alert('Start time must be before end time');

      let request = {
        user: participantList,
        startTime: startTime.getTime().toString(),
        endTime: endTime.getTime().toString(),
      };
      // eslint-disable-next-line
      await axios.post(createInterview, request);
      const start = new Date();
      const end = new Date();
      setStartTime(start);
      setEndTime(end);
      alert('Interview was successfully created');
      navigate('/');
    } catch (error) {
      alert(error.response.data.error);
    }
  };

  return loading ? (
    <h1>Loading ... Please wait!!</h1>
  ) : (
    <main>
      <div className='relative px-6 lg:px-8'>
        <div className='max-w-3xl pb-32 mx-auto pt-36 sm:pt-48 sm:pb-40'>
          <div>
            <div>
              <h1 className='flex justify-center pb-10 text-4xl font-bold tracking-tight sm:text-center sm:text-6xl'>
                Add Interview Details
              </h1>
              <div className='flex justify-center mt-6 text-lg leading-8 text-gray-600 sm:text-center sm:flex sm:items-center'>
                <form className='w-full max-w-sm'>
                  <div className='mb-6 md:flex md:items-center'>
                    <div className='md:w-28'>
                      <label
                        className='block pr-4 mb-1 font-bold text-gray-500 md:text-right md:mb-0'
                        htmlFor='inline-full-name'
                      >
                        Start Time
                      </label>
                    </div>
                    <div className='md:w-72'>
                      {
                        <DateTimePicker
                          onChange={setStartTime}
                          value={startTime}
                          className='w-full leading-tight text-gray-700 border-2 border-gray-200 rounded appearance-none focus:outline-none focus:bg-white focus:border-purple-500'
                        />
                      }
                    </div>
                  </div>
                  <div className='mb-6 md:flex md:items-center'>
                    <div className='md:w-28'>
                      <label
                        className='block pr-4 mb-1 font-bold text-gray-500 md:text-right md:mb-0'
                        htmlFor='inline-full-name'
                      >
                        End Time
                      </label>
                    </div>
                    <div className='md:w-72'>
                      {
                        <DateTimePicker
                          onChange={setEndTime}
                          value={endTime}
                          className='w-full leading-tight text-gray-700 border-2 border-gray-200 rounded appearance-none focus:outline-none focus:bg-white focus:border-purple-500'
                        />
                      }
                    </div>
                  </div>
                  <div className='mb-6 md:flex md:items-center'>
                    <div className='md:w-1/3'>
                      <label
                        className='block pr-4 mb-1 font-bold text-gray-500 md:text-right md:mb-0'
                        htmlFor='inline-password'
                      >
                        Select Participants
                      </label>
                    </div>
                    <div className='md:w-2/3'>
                      <Multiselect
                        options={userList}
                        placeholder='Select participants'
                        classNameName='w-full px-4 py-2 leading-tight text-gray-700 bg-gray-200 border-2 border-gray-200 rounded appearance-none focus:outline-none focus:bg-white focus:border-purple-500'
                        displayValue='name'
                        onSelect={(e, t) => {
                          let p = [t, ...participantList];
                          setParticipantList(p);
                        }}
                        onRemove={(e, t) => {
                          setParticipantList(
                            participantList.filter((x) => x !== t)
                          );
                        }}
                      />
                    </div>
                  </div>

                  <div className='pt-6 md:flex md:items-center'>
                    <div className='md:w-1/3'></div>
                    <div className='md:w-2/3'>
                      <button
                        className='px-4 py-2 font-bold text-white bg-purple-500 rounded shadow hover:bg-purple-400 focus:shadow-outline focus:outline-none'
                        type='button'
                        onClick={submit}
                      >
                        Create
                      </button>
                    </div>
                  </div>
                </form>
              </div>
            </div>
            <div className='absolute inset-x-0 top-[calc(100%-13rem)] -z-10 transform-gpu overflow-hidden blur-3xl sm:top-[calc(100%-30rem)]'>
              <svg
                className='relative left-[calc(50%+3rem)] h-[21.1875rem] max-w-none -translate-x-1/2 sm:left-[calc(50%+36rem)] sm:h-[42.375rem]'
                viewBox='0 0 1155 678'
                fill='none'
                xmlns='http://www.w3.org/2000/svg'
              >
                <path
                  fill='url(#ecb5b0c9-546c-4772-8c71-4d3f06d544bc)'
                  fillOpacity='.3'
                  d='M317.219 518.975L203.852 678 0 438.341l317.219 80.634 204.172-286.402c1.307 132.337 45.083 346.658 209.733 145.248C936.936 126.058 882.053-94.234 1031.02 41.331c119.18 108.451 130.68 295.337 121.53 375.223L855 299l21.173 362.054-558.954-142.079z'
                />
                <defs>
                  <linearGradient
                    id='ecb5b0c9-546c-4772-8c71-4d3f06d544bc'
                    x1='1155.49'
                    x2='-78.208'
                    y1='.177'
                    y2='474.645'
                    gradientUnits='userSpaceOnUse'
                  >
                    <stop stopColor='#9089FC'></stop>
                    <stop offset='1' stopColor='#FF80B5'></stop>
                  </linearGradient>
                </defs>
              </svg>
            </div>
          </div>
        </div>
      </div>
    </main>
  );
};

export default AddScreen;
