import React from 'react';
import { Link } from 'react-router-dom';

const Interview = ({ item }) => {
  const date = new Date(parseInt(item.startTime, 10)).toLocaleDateString();
  const startTime = new Date(parseInt(item.startTime, 10)).toLocaleTimeString(
    'en-IN',
    { hour12: false, hour: 'numeric', minute: 'numeric' }
  );
  const endTime = new Date(parseInt(item.endTime, 10)).toLocaleTimeString(
    'en-IN',
    { hour12: false, hour: 'numeric', minute: 'numeric' }
  );
  return (
    <div className='flex flex-wrap justify-center my-10 overflow-hidden rounded-md shadow-md'>
      <div className='px-6 py-4 w-100 sm:w-1/3'>
        <div className='mb-2 text-xl font-bold'>
          <div>{date}</div>
          <p>
            {startTime} - {endTime}
          </p>
        </div>
        <Link
          to={`/edit/${item._id}`}
          className='inline-block rounded-lg bg-indigo-600 px-4 py-1.5 text-base font-semibold leading-7 text-white shadow-sm ring-1 ring-indigo-600 hover:bg-indigo-700 hover:ring-indigo-700'
        >
          Edit Details
          <span className='text-indigo-200' aria-hidden='true'>
            &rarr;
          </span>
        </Link>
      </div>
      <div className='mt-5 mb-3 ml-10 w-100 sm:w-2/3'>
        {item.email.map((email, index) => (
          <span
            className='inline-block px-3 py-1 mb-2 mr-2 text-sm font-semibold text-gray-700 bg-gray-200 rounded-full'
            key={index}
          >
            {email}
          </span>
        ))}
      </div>
    </div>
  );
};

export default Interview;
