import React from 'react';
import { Link } from 'react-router-dom';

const Header = () => {
  let Links = [
    {
      name: 'HOME',
      link: '/',
    },
    {
      name: 'ADD',
      link: '/add',
    },
  ];

  return (
    <div className='fixed top-0 left-0 z-10 w-full shadow-wd bg-gradient-to-r from-pink-400 via-purple-400 to-indigo-500'>
      <div className='flex flex-wrap items-start justify-center py-5 sm:justify-evenly'>
        <div className='flex justify-center w-full text-2xl font-extrabold text-white cursor-pointer sm:w-auto'>
          <Link to='/'>Project</Link>
        </div>
        <div className='sm:flex sm:justify-center sm:space-x-5'>
          {Links.map((link) => (
            <Link
              to={link.link}
              key={link.name}
              className='text-2xl text-gray-800 duration-500 md:ml-8 hover:text-white'
            >
              {' '}
              {link.name}
            </Link>
          ))}
        </div>
      </div>
    </div>
  );
};

export default Header;
